import React from 'react'
import { Card } from 'antd'
import {typing} from '../../utils/utils.js'

class TypingCard2 extends React.Component {
  static defaultProps = {
    title: '',
    source: '<p>.</p>'
      + '<p>.</p>'
      + '<p>.</p>'
      + '<p>.</p>'
      + '<p>.</p>'
      + '<p>.</p>',
    speed: 300,
    height: 136
  }
  componentDidMount() {
    typing(this.refs.typing, this.props.speed, this.props.source)
  }
  render() {
    return (
      <Card title={this.props.title} style={{ minHeight: this.props.height }} id={this.props.id} hoverable>
        <div ref='typing'></div>
      </Card>
    )
  }
}

export default TypingCard2



